import { Box, Typography, Grid } from '@mui/material';
import MinorHeader from '../components/Header/MinorHeader';
import PriceItem from '../components/PriceList/PriceItem';
import { styleBox, PortfolioButton } from '../CustomStyles';

const rodinkyAdeti = {
  text: (
    <>
      <p>Dti, pry, thulky, portrty</p> 
      <p>10 ks upravench fotografi v elektronick podob</p>
      <p>Soukrom online galerie</p>
      <p>1 hodina focen </p>
    </>
  ),
  price: (
    <>
      <p>Exterir: 2.400,-</p>
      <p>Atelir: 3.400,-</p>
    </>
  ),
};

const miminka = {
  text: (
    <>
      <p>Lifestylov nebo stylizovan focen</p>
      <p>2-4 hodiny focen</p>
      <p>Soukrom online galerie</p>
      <p>10ks upravench fotografi v elektronick podob</p>
    </>
  ),
  price: (
    <>
      <p>Focen u vs doma: 2.900,-</p>
      <p>Atelir: 3.900,-</p>
    </>
  ),
};

const svatby = {
  text: (
    <>
      <p>V ppad Vaeho zjmu o zasln cenov nabdky mi prosm napite zprvu spolen s termnem a mstem konn svatby.</p>
    </>
  ),
  price: (
    <>
      <p>
        Svatebn focen zan na
        <br />stce 13.900,-
      </p>
    </>
  ),
};



const PriceList = () => {
  return(
    <>
      <Box sx={styleBox}>
        <MinorHeader minHeight="40px"/>
        <Grid container justifyContent="center" >
          <Grid item>
            <PriceItem title="Rodinky a dti" text={rodinkyAdeti.text} price={rodinkyAdeti.price}/>
          </Grid>
          <Grid item>
            <PriceItem title="miminka" text={miminka.text} price={miminka.price}/>
          </Grid>
          <Grid item>
            <PriceItem title="svatby" text={svatby.text} price={svatby.price}/>
          </Grid>
        </Grid>
        <Box sx={{ paddingTop: '20px' }}>
          <Typography variant="body1">Fotografie si vybrte z nhledov online galerie.</Typography>
          <br />
          <Typography variant="body1">Kad dal fotografie navc + 200k</Typography>
          <br />
          <Typography variant="body1">Cesta do 15km od Holic zdarma, pot + 9k/km</Typography>
          <br />
          <Typography variant="body1">Cenk je platn od 01.04.2023 </Typography>
        </Box>
        <PortfolioButton variant='contained' href="/kontakt">
            Napite mi
        </PortfolioButton>
      </Box>
      <Box sx={{ height: '70px' }}/>
    </>
  );
};

export default PriceList;
