import { Box, Typography } from '@mui/material';
import { PortfolioButton, PortfolioBox, styleBox35padding } from '../CustomStyles';


const Miminka = () => {
  return (
    <>
      <Box sx={styleBox35padding}>
        <PortfolioBox sx={{ flexDirection: 'row-reverse', paddingBottom: '50px' }}>
            <Box sx={{ width: '500px' }}>
                <img alt="jmeno" src="/images/babies/DSC_0091.jpg" style={{ width: '100%', height: '500px', objectFit: 'contain' }}/>
            </Box>
            <Box sx={{ width: '500px', textAlign: 'center' }}>
                <Typography variant='body1' sx={{ paddingTop: '15px' }}>
                  Na focen miminka se prosm objednvejte ji v thotenstv. Tmto pedobjednnm si Vs zapu a pesn termn doladme, a bude miminko na svt.
                  <br />
                  Novorozeneck fotky fotm mezi 5. a 14. dnem od narozen.
                </Typography>
            </Box>
        </PortfolioBox>
        <Box>
          <Typography variant='h3'>Podrobnj informace</Typography>
          <Typography variant='body1' sx={{ paddingTop: '15px' }}>
            Focen probh z pohodl Vaeho domova. 
            <br />
            Pi objednn mi prosm napite, zda budete mt zjem o stylizovan focen, nebo tzv. Lifestyle
            <br />
            <br />
            <b>Lifestylov focen</b> je fotografovn bez rekvizit, pouze Vy a miminko.
            <br />
            <b>Stylizovan fotografovn</b> je aranovn miminka - focen v kokch, oblekch apod.
            <br />
            <br />
            Veker potebn rekvizity dovezu s sebou. Od Vs budu pouze potebovat pouze vytopenou a svtlej mstnost, vytopenou ideln kolem 26-27stup.
            <br />
            <br />
            Doba focen je zvisl na miminku, focen budeme malikmu pln pizpsobovat. Budeme nkolikrt kojit, pebalovat. Potejte tedy s del dobou focen cca 2-3 hodiny.
            <br />
            <br />
            <b>Jak se na focen pipravit?</b>
            <br />
            Svtl, formlnj obleen pro vechny fotografovan (krom miminka). Pro tatnky a bratky miminka doporuuji blou koili s dlouhm rukvem, pro maminky blou halenku, aty Boty nejsou na focen poteba. Obleen pro novorozetko mm.
            <br />
            Maminky a holiky, nebojte se na focen krsn ,, naanat" uvidte, e ppravu ocente. Holikm doporuuji natoit vlsky, hezky uesat. Nezapomete na pravu neht, nemus bt uml, sta ist, upraven..
            <br />
            <br />
            Pro miminko: dudlk (focen s nm je o dost snaz, miminko je pi dudln klidnj) a dostatek umlho mlka, jestlie nemte dostatek mateskho.
            <br />
            <br />
            <b>Doba dodn fotografi</b>
            <br />
            Nhledy zaslm do 10 pracovnch dn.
            <br />
            Nhledy jsou zmenen, neupraven fotografie, neslou k dalmu en, stahovn apod.
            <br />
            <b>Neupraven fotografie neposkytuji, proto je po mn prosm ani nedejte.</b>
            <br />
            <br />
            prava zahrnuje barvy, retu pleti, oez, tnovn. Neupravuji tlesn proporce, vrsky, dtsk tetovaky, nplasti.
            <br />
            Svtlost/tmavost, barevn ndech a fotografick styl nen mon reklamovat ani v elektronick, ani titn podob.
            <br />
            <u>Dvodem k reklamaci fotografi nen nespokojenost s vlastnm vzhledem.</u>
            <br />
            Upraven fotografie poslm do 8 tdn od doby, kdy mi polete svj vbr k prav.
            <br />
            Doba dodn se li dle aktuln vytenosti. Fotografie zaslm elektronicky.
          </Typography>
          <PortfolioButton variant='contained' href='/kontakt'>
              Kontakt
          </PortfolioButton>
        </Box>
      </Box>
    </>
  )
}

export default Miminka;
