import { Box, Typography } from '@mui/material';
import { PortfolioButton, PortfolioBox, styleBox35padding } from '../CustomStyles';


const Deti = () => {
  return (
    <>
      <Box sx={styleBox35padding}>
        <PortfolioBox sx={{ flexDirection: 'row-reverse', paddingBottom: '50px' }}>
            <Box sx={{ width: '500px' }}>
                <img alt="jmeno" src="/images/kids/DSC_6486.jpg" style={{ width: '100%', height: '500px', objectFit: 'contain' }}/>
            </Box>
            <Box sx={{ width: '500px', textAlign: 'center' }}>
                <Typography variant='body1' sx={{ paddingTop: '15px' }}>
                  Focen rodinek a dt me probhat v exteriru nebo ve Vmi vybranm ateliru v Hradci Krlov nebo v Pardubicch.
                  <br />
                  Pi venkovnm rodinnm focen je samozejmost i focen s domcmi mazlky.
                </Typography>
            </Box>
        </PortfolioBox>
        <Box>
          <Typography variant='h3'>Informace o focen dt</Typography>
          <Typography variant='body1' sx={{ paddingTop: '15px' }}>
            Focen dt je ideln od vku cca 4 msc, kdy u vydr hezky "pasou" konky.
            <br />
            Focen rodinek a dt me probhat v exteriru nebo ve Vmi vybranm ateliru v Hradci Krlov nebo v Pardubicch. 
            <br />
            Pi venkovnm rodinnm focen je samozejmost i focen s domcmi mazlky.
            <br />
            <br />
            <b>Co na sebe, Co s sebou?</b>
            <br />
            <b>Obleen:</b> Doporuuji bez velkch npis, ruivch element, log apod. Vhodn je slavnostnj obleen nebo prodnj styl.
            <br />
            <br />
            <b>Mui:</b> Koile s dlouhm rukvem, jednobarevn triko, rifle, koile, svetr.
            <br />
            <b>eny:</b> aty, koile, tunika, svetr, legny.
            <br />
            <br />
            <b>Dtem vezmte oblbenou hraku pro upoutn pozornosti a odmnu za focen :-)</b>
            <br />
            <br />
            Maminky a holiky, nebojte se na focen krsn ,, naanat" uvidte, e ppravu ocente. Holikm doporuuji natoit vlsky, hezky uesat.
            <br />
            Nezapomete na pravu neht, nemus bt uml, sta ist, upraven.. 
            <br />
            <br />
            <b>Doba dodn fotografi</b>
            <br />
            Nhledy zaslm do 10 pracovnch dn.
            <br />
            Nhledy jsou zmenen, neupraven fotografie, neslou k dalmu en, stahovn apod.
            <br />
            <b>Neupraven fotografie neposkytuji, proto je po mn prosm ani nedejte.</b>
            <br />
            <br />
            prava zahrnuje barvy, retu pleti, oez, tnovn. Neupravuji tlesn proporce, vrsky, dtsk tetovaky, nplasti.
            <br />
            Svtlost/tmavost, barevn ndech a fotografick styl nen mon reklamovat ani v elektronick, ani titn podob.
            <br />
            <u>Dvodem k reklamaci fotografi nen nespokojenost s vlastnm vzhledem.</u>
            <br />
            Upraven fotografie poslm do 8 tdn od doby, kdy mi polete svj vbr k prav.
            <br />
            Doba dodn se li dle aktuln vytenosti. Fotografie zaslm elektronicky.
          </Typography>
          <PortfolioButton variant='contained' href='/kontakt'>
              Kontakt
          </PortfolioButton>
        </Box>
      </Box>
    </>
  )
}

export default Deti;
