import { Box, Typography, Avatar } from '@mui/material';
import { BannerBox } from '../../CustomStyles';
import { useWindow } from '../../Context';

const Banner = () => {
    const { windowSize } = useWindow();

    return (
        <BannerBox>
            <Box sx={{ maxWidth: '500px' }}>
                <Avatar alt="LucieFojtova" src='/Profil.jpg' sx={{ width: 350, height: 350 }} />
            </Box>
            <Box sx={{ maxWidth: '700px' }}>
                <Typography variant='body1' sx={{ padding: '0px 00px 35px 0px', textAlign: 'center', maxWidth: '600px' }}>
                    Ahoj. Jsem Lucie a ji nkolik let miluji zvuk zmknut spout.
                    V hledku svho fotoapartu mm nejastji rodinky, miminka a svatby. 
                </Typography>
                <Typography sx={{ padding: '0px 15px', textAlign: 'center', fontFamily: 'Corinthia, cursive', fontSize: windowSize >= 1050 ? '40px' : '30px' }}>
                    Jedin fotografie, kter budete litovat, je ta, kterou nemte.
                </Typography>
            </Box>
        </BannerBox>
    );
};

export default Banner;