import { Box, Typography } from '@mui/material';
import { PortfolioButton, PortfolioBox, styleBox35padding } from '../CustomStyles';

const Svatby = () => {
  return (
    <>
      <Box sx={styleBox35padding}>
        <PortfolioBox sx={{ flexDirection: 'row-reverse', paddingBottom: '50px' }}>
            <Box sx={{ width: '500px' }}>
                <img alt="jmeno" src="/images/svatba/183.jpg" style={{ width: '100%', height: '500px', objectFit: 'contain' }}/>
            </Box>
            <Box sx={{ width: '500px', textAlign: 'center' }}>
                <Typography variant='body1' sx={{ paddingTop: '15px' }}>
                  Hledte svatebnho fotografa pro V velk den? Rda se stanu soust Vaeho dne a zachytm kad okamik.
                  <br />
                  <br />
                  Ke kadmu focen pistupuji individuln, v ppad Vaeho zjmu o zasln cenov nabdky mi prosm napite zprvu spolen s termnem a mstem konn svatby.
                  <br />
                  <br />
                  Svatebn focen zan na stce 13.900,- 
                </Typography>
            </Box>
        </PortfolioBox>
        <Box>
          <Typography variant='h3'>Dleit informace</Typography>
          <Typography variant='body1' sx={{ paddingTop: '15px' }}>
          Fotografie dodvm vdy do 8 tdn od svatby. Poet fotografi se li dle vybranho balku.
          <br />
          <br />
          Zaruuji dodn prvnch cca 10ks fotografi do tdne od svatby, dal fotografie zaslm postupn nahrnm do Va soukrom online galerie.
          <br />
          <strong>Fotografie vybrm sama a neupraven fotografie neposkytuji, proto je po m, prosm, ani nedejte.</strong>
          <br />
          <br />
          V cen mte vdy pedsvatebn schzku, pi tto pleitosti nabzm pedsvatebn focen zdarma. Je to ideln monost ,,vyzkouet si focen a lpe se poznat.
          <br />
          <br />
          <strong>Pro zvaznou rezervaci termnu se hrad zloha ve vi 2.000,-</strong>
          <br />
          <br />
          </Typography>
          <PortfolioButton variant='contained' href='/kontakt'>
              Kontakt
          </PortfolioButton>
        </Box>
      </Box>
    </>
  )
}

export default Svatby;
