import Header from '../components/Header/Header';
import DividerText from '../components/Divider/Divider';
import PortfolioItem from '../components/PortfolioComponents/PorfolioItem';
import Banner from '../components/Banner/Banner';
import ContactForm from '../components/ContactForm/ContactForm';
import { Box } from '@mui/material';
import { styleBox35padding } from '../CustomStyles';

const text = {
  svatby: 
    'Velmi rda se stanu soust Vaeho neopakovatelnho pbhu. Zaujala Vs moje prce? Kouknte do portfolia a v ppad zjmu m kontaktujte. Ke kad svatb pistupuji individuln!',
  miminka: 
    'Miminka fotm ideln do 14 dn od jejich narozen. Focen probh z pohodl Vaeho domova, nebo ve specializovanm ateliru v Pardubicch nebo Hradci Krlov. Zda vyberete stylizovan fotografie nebo pirozen tzv. Lifestyle, nechm na Vs.',
  deti: 'Focen dt je ideln od vku cca 4 msc, kdy u vydr na bku, a do dosplosti. Kad obdob m sv kouzlo.',
  rodinky: 'Tady plat dvakrt tolik, e jedin fotka, kter budete litovat je ta, kterou nemte! Na rodinn focen je vhodn doba pln vdy. Pijt mete s celou rodinkou, sourozencem nebo teba s babikou.',
}

const HomePage = () => {
   return (
    <>
      <Header minHeight="750px" />
      <Banner />
      <DividerText message="co fotm" />
      <Box sx={styleBox35padding}>
        <PortfolioItem 
          direction="" 
          name="svatby"
          type="svatby"
          image="/images/svatba/svatba_uvod_fit.jpg" 
          text={text.svatby}
        />
        <PortfolioItem 
          direction="row-reverse" 
          name="miminka"
          type="miminka"
          image="/images/babies/miminka_uvod.jpg" 
          text={text.miminka}
        />
        <PortfolioItem 
          direction="" 
          name="dti"
          type="detiarodinky"
          image="/images/kids/Deti_uvod.jpg" 
          text={text.deti}
        />
        <PortfolioItem 
          direction="row-reverse" 
          name="rodinky"
          type="detiarodinky"
          image="/images/rodinky/rodinky_uvod.jpg" 
          text={text.rodinky}
        />
      </Box>
      <DividerText message="kontakt" />
      <Box sx={styleBox35padding}>
        <ContactForm />
      </Box>
    </>
  );
}

export default HomePage;
